'use strict';

/**
* Chi distribution cumulative distribution function (CDF).
*
* @module @stdlib/stats-base-dists-chi-cdf
*
* @example
* import cdf from '@stdlib/stats-base-dists-chi-cdf' ;
*
* var y = cdf( 2.0, 8.0 );
* // returns ~0.143
*
* y = cdf( 0.0, 1.0 );
* // returns 0.0
*
* @example
* import { factory as factory } from '@stdlib/stats-base-dists-chi-cdf' ;
*
* var cdf = factory( 1.0 );
*
* var y = cdf( 2.0 );
* // returns ~0.954
*
* y = cdf( 1.2 );
* // returns ~0.77
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
